// import { supabase } from '../../lib/supabase';
import ProjectCard from '@/components/ui/ProjectCard';

const projects = [
  {
    id: 1,
    title: "Data Analytics Dashboard",
    description: "A real-time dashboard for consulting metrics.",
    category: "Data"
  },
  {
    id: 2,
    title: "Supply Chain Strategy",
    description: "Optimization of logistics for a global retail brand.",
    category: "Consulting"
  }
];

export default async function ProjectsPage() {
  // This fetch happens at BUILD TIME (Static Rendering)
  // const { data: projects } = await supabase.from('projects').select('*');

  return (
    <main className="max-w-7xl mx-auto py-20">
      <h1 className="text-4xl font-bold">Our Projects</h1>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {projects?.map((project) => (
          <ProjectCard key={project.id} {...project} />
        ))}
      </div>
    </main>
  );
}