import React from 'react';
import { ArrowRight } from 'lucide-react';

export default function ProjectCard({ title, description, category }) {
  return (
    <div className="p-6 border rounded-2xl bg-white shadow-sm hover:shadow-md transition-shadow">
      <span className="text-xs font-bold text-blue-600 uppercase">{category}</span>
      <h3 className="text-xl font-bold mt-2">{title}</h3>
      <p className="text-gray-600 mt-2">{description}</p>
      <button className="mt-4 flex items-center gap-2 text-blue-600 font-medium">
        Learn More <ArrowRight size={16} />
      </button>
    </div>
  );
}